@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.book-title {
  margin: 1rem;
  text-align: center;
  color: #eee;
  text-shadow: 2px 2px #333;
}

.title {
  font-size: 20px;
}

.small-title {
  color: #eee;
  text-shadow: 2px 2px #333;
  text-align: center;
  display: block;
  margin-bottom: 1rem;
}

.cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  justify-content: center;
  justify-items: center;
  text-align: center;
  grid-gap: 2rem;
  gap: 2rem;
}

.img-container {
  position: relative;
  overflow: hidden;
}

.container {
  position: absolute;
  background-color: rgba(255, 249, 249, 0.3);
  color: rgb(36, 35, 35);
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  -webkit-transform: translateY(101%);
          transform: translateY(101%);
  border-radius: 30px 30px 0 0;
  width: 100%;
  bottom: 0;
  padding: 10px;
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
}

.get-books {
  margin: 0.5rem;
}

.link {
  color: #333;
  text-decoration: none;
}

.small {
  font-size: 0.8rem;
  margin: 1rem;
}

.link:hover {
  background-color: #333;
  border-radius: 10px;
  padding: 0 1rem;
  color: #fff;
}

.img-container:hover .container {
  -webkit-transform: translateY(0);
          transform: translateY(0);
}

.author {
  margin: 1rem;
  padding: 1rem;
  border-bottom: 3px solid #333;
}

.img {
  width: 300px;
  height: 450px;
}

.skeleton {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: center;
  justify-items: center;
  text-align: center;
  grid-gap: 3rem;
  gap: 3rem;
}

.placeholder-item {
  box-shadow: 0 4px 10px 0 rgba(33, 33, 33, 0.15);
  border-radius: 4px;
  width: 300px;
  height: 450px;
  background-color: #d1cbcb;
  position: relative;
  overflow: hidden;
}

.placeholder-item::before {
  content: "";
  display: block;
  position: absolute;
  left: -150px;
  top: 0;
  height: 100%;
  width: 150px;
  background: linear-gradient(
    to right,
    rgba(255, 255, 255, 0) 0%,
    #e8e8e8 50%,
    rgba(255, 255, 255, 0) 100%
  );
  -webkit-animation: load 1s cubic-bezier(0.4, 0, 0.2, 1) infinite;
          animation: load 1s cubic-bezier(0.4, 0, 0.2, 1) infinite;
}

.load-title {
  margin: 2rem auto;
  box-shadow: 0 4px 10px 0 rgba(33, 33, 33, 0.15);
  border-radius: 4px;
  width: 300px;
  height: 30px;
  background-color: #d1cbcb;
  position: relative;
  overflow: hidden;
}

.load-title::before {
  content: "";
  display: block;
  position: absolute;
  left: -150px;
  top: 0;
  height: 100%;
  width: 150px;
  background: linear-gradient(
    to right,
    rgba(255, 255, 255, 0) 0%,
    #e8e8e8 50%,
    rgba(255, 255, 255, 0) 100%
  );
  -webkit-animation: load 1s cubic-bezier(0.4, 0, 0.2, 1) infinite;
          animation: load 1s cubic-bezier(0.4, 0, 0.2, 1) infinite;
}

.load-small {
  margin: 0.8rem auto;
  box-shadow: 0 4px 10px 0 rgba(33, 33, 33, 0.15);
  border-radius: 4px;
  width: 150px;
  height: 10px;
  background-color: #d1cbcb;
  position: relative;
  overflow: hidden;
}

.load-small::before {
  content: "";
  display: block;
  position: absolute;
  left: -150px;
  top: 0;
  height: 100%;
  width: 150px;
  background: linear-gradient(
    to right,
    rgba(255, 255, 255, 0) 0%,
    #e8e8e8 50%,
    rgba(255, 255, 255, 0) 100%
  );
  -webkit-animation: load 1s cubic-bezier(0.4, 0, 0.2, 1) infinite;
          animation: load 1s cubic-bezier(0.4, 0, 0.2, 1) infinite;
}

@-webkit-keyframes load {
  from {
    left: -150px;
  }
  to {
    left: 100%;
  }
}

@keyframes load {
  from {
    left: -150px;
  }
  to {
    left: 100%;
  }
}

@media only screen and (max-width: 850px) {
  .skeleton {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media only screen and (max-width: 400px) {
  .skeleton {
    grid-template-columns: repeat(1, 1fr);
  }
}

.container-card {
  text-align: center;
  margin: 1rem;
  color: #333;
  background-color: rgba(198, 201, 200, 0.452);
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  box-shadow: 4px 3px 8px rgba(0, 0, 0, 0.7);
  border-radius: 30px;
  padding: 10px;
}

.btn {
  text-transform: uppercase;
  font-weight: bold;
  padding: 0 30px;
  color: #eee;
  text-shadow: 2px 2px #333;
}

.arrow {
  border: solid #eee;
  box-shadow: 2px 1px 3px #333;
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 3px;
}

.down {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  transition: -webkit-transform 0.4s;
  transition: transform 0.4s;
  transition: transform 0.4s, -webkit-transform 0.4s;
  -webkit-transition: -webkit-transform 0.4s;
}

.up {
  transform: rotate(-135deg);
  -webkit-transform: rotate(-135deg);
  transition: -webkit-transform 0.4s;
  transition: transform 0.4s;
  transition: transform 0.4s, -webkit-transform 0.4s;
  -webkit-transition: -webkit-transform 0.4s;
}

.container-card:hover {
  cursor: pointer;
  background-color: rgba(154, 156, 156, 0.452);
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
}

.container-info {
  display: inline-block;
  background-color: #333;
  border-radius: 15px;
  color: #fff;
  padding: 0.3rem 0.5rem;
  margin: 0.2rem;
}

.container-info:hover {
  cursor: pointer;
  background-color: rgb(27, 27, 27);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
  background-image: url(/best-selling-books/static/media/bg.6d8099a5.jpg);
  background-attachment: fixed;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

@media only screen and (max-width: 850px) {
  body:before {
    content: "";
    display: block;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: -10;
    background: url(/best-selling-books/static/media/bg.6d8099a5.jpg) no-repeat center center;
    background-size: cover;
  }
}

@media only screen and (orientation: landscape) {
  body:before {
    content: "";
    display: block;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: -10;
    background: url(/best-selling-books/static/media/bg.6d8099a5.jpg) no-repeat center center;
    background-size: cover;
  }
}

